.Perception-form {
  background-color: #333;
  padding: 20px;
  margin-bottom: 10px;
}

input[type="number"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 5px;
  margin-top: 6px;
  margin-bottom: 16px;
}

select {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-top: 6px;
  margin-bottom: 16px;
}

button {
  background-color: #4caf50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}



